@font-face {
  font-family: GT America;
  src: url("GT-America-Standard-Regular.80e0db52.otf") format("opentype");
}

@font-face {
  font-family: GT America Bold;
  src: url("GT-America-Standard-Bold.004e54b8.otf") format("opentype");
}

@font-face {
  font-family: GT America Condensed;
  src: url("GT-America-Condensed-Regular.7a423f65.otf") format("opentype");
}

body {
  color: #eee;
  -font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #000;
  font-family: GT America;
  font-size: 13px;
}

h3.title {
  text-align: center;
  margin-top: 0;
  margin-bottom: 20px;
  font-family: GT America Bold;
  font-size: 18px;
  line-height: 28px;
}

.container {
  max-width: 600px;
  margin: auto;
}

.hidden {
  display: none;
}

.footnote {
  color: #ffffffbf;
  text-align: left;
  max-width: 600px;
  margin: 40px auto;
  font-family: GT America;
  font-size: 12px;
}

.footnote.graph-footnote {
  margin-top: 100px;
}

.footnote.map-footnote {
  margin-top: 0;
}

.mobile-only {
  display: none;
}

.btn-text {
  color: #fff;
  text-align: center;
  border-radius: 3px;
  font-family: GT America Bold;
  font-size: 11px;
  font-weight: 600;
}

.outliers-btn {
  cursor: pointer;
  user-select: none;
  text-transform: uppercase;
  border: 1px solid #333;
  margin-left: 1em;
  padding: 10px 15px;
}

.outliers-btn.showing {
  color: #fff;
  background-color: #1d1d1d;
}

.switch-container {
  cursor: pointer;
  user-select: none;
  justify-content: center;
  margin: 1.5em 0;
  display: flex;
}

.switch-container .switch {
  text-transform: uppercase;
  border: 1px solid #333;
  width: 130px;
  padding: 10px;
}

.switch.left {
  border-radius: 3px 0 0 3px;
}

.switch.right {
  border-left: 0;
  border-radius: 0 3px 3px 0;
}

.switch-container .switch.showing, .switch-container .switch:hover {
  background-color: #1d1d1d;
}

.county-dist-table-container {
  grid-template-columns: 50% 50%;
  gap: 24px;
  max-width: 100%;
  margin-bottom: 30px;
  display: grid;
}

.county-dist-table-container table {
  border-collapse: collapse;
  width: 100%;
  display: contents;
}

.county-dist-table-container tbody, .county-dist-table-container tr {
  display: contents;
}

.county-dist-table-container td {
  width: 100%;
  padding: 10px 10px 30px;
  font-size: 16px;
  display: block;
  text-align: left !important;
  border-left: 0 !important;
  border-right: 0 !important;
}

@media (width <= 600px) {
  .county-dist-table-container {
    grid-template-columns: 100%;
  }
}

.county-dist-table-container td .stat-tile-label {
  color: #fff;
  letter-spacing: .5px;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 12px;
  font-family: Gt america condensed, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.county-dist-table-container td .stat-tile-value {
  color: #fff;
  margin-bottom: 10px;
  font-family: Centuryschoolbook, Times New Roman, sans-serif;
  font-size: 38px;
  line-height: 26px;
}

.full-span {
  grid-column: span 2;
}

.county-dist-table-container > table {
  margin-bottom: 60px;
}

.county-dist-table-container > table td {
  border-top: 1px solid #333;
  border-bottom: 0 !important;
}

.county-dist-table-container {
  display: block;
}

.county-dist-table-container table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  display: table;
}

.county-dist-table-container thead {
  display: none;
}

.county-dist-table-container tbody {
  grid-template-columns: 1fr 1fr;
  gap: 0 12px;
  display: grid !important;
}

.county-dist-table-container tr {
  display: contents;
}

.county-dist-table-container td {
  box-sizing: border-box;
  border-top: 1px solid #333;
  width: 100%;
  margin: 0;
  display: block;
  padding-top: 14px !important;
}

.county-dist-table-container .stat-tile-label, .county-dist-table-container .stat-tile-value {
  margin: 0;
}

@media (width <= 600px) {
  .county-dist-table-container tbody {
    grid-template-columns: 1fr;
  }
}

.county-dist-table-container {
  margin-bottom: 60px;
}

.county-dist-table-container h2.bucket-name {
  color: #fff;
  margin-top: 0;
  margin-bottom: 15px;
  font-family: Centuryschoolbook, Times New Roman, sans-serif;
  font-size: 32px;
  font-weight: 400;
  line-height: 32px;
}
